@import url(https://fonts.googleapis.com/css?family=Coming+Soon|Londrina+Outline&display=swap);
.display {
  padding: 16px;
  background-color: rgba(206, 66, 87, 0.1);
  font-size: 20px;
  color: #c3c3c3;
  box-shadow: 0 0 2px 1px rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  width: 70%;
  min-height: 32px;
  margin: 0 auto; }

.display p {
  margin: 0;
  font-family: "Coming Soon", cursive;
  font-weight: bold; }

.controls-wrapper {
  padding: 15px 0 25px 0; }

.control-row {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.control-col-1 {
  flex: 1 1; }

.control-col-2 {
  flex: 2 1; }

.control-col-3 {
  flex: 3 1; }

.power-btn {
  border-radius: 50%;
  border: none;
  outline: none;
  padding: 7.5px 4px;
  background-color: rgba(206, 66, 87, 0.6);
  box-shadow: 0 0 3px 2px #c3c3c3; }

.power-icon {
  color: #b3b3b3; }

.volume-icon {
  color: rgba(206, 66, 87, 0.6); }

.active {
  box-shadow: 0 0 3px 1px #39ff14, 0 0 2px 0 #fff, 0 0 3px 0px #ce4257, 0 0 6px 1px #39ff14, 0 0 9px 0 #39ff14; }

.active .power-icon {
  color: #39ff14; }

.volume-btn {
  background: transparent;
  border: none;
  outline: none; }

input#volume {
  width: 65%; }

.select-kit select {
  color: #c3c3c3;
  padding: 7px 16px;
  background: rgba(206, 66, 87, 0.6);
  border-radius: 6px;
  font-size: 18px;
  outline: none;
  border: 1px solid rgba(206, 66, 87, 0.6);
  margin-left: 16px; }

.select-kit option {
  color: #1b1b1b;
  font-size: 18px; }

.btn {
  padding: 15px 25px;
  height: 60px;
  width: 80px;
  text-transform: uppercase;
  font-size: 25px;
  font-weight: bold;
  border: 0;
  outline: none;
  background: -webkit-gradient(linear, left bottom, left top, from(#482969), to(#482969));
  background: linear-gradient(0deg, #482969, #482969);
  color: #ffffff;
  border-radius: 6px;
  box-shadow: 0 0 3px 0 #ce4257;
  text-shadow: 0 0 3px #ce4257, 0 0 5px #ce4257, 0 0 15px #ce4257, 0 0 25px #ce4257; }

.btn:active,
.active-key {
  color: #39ff14;
  box-shadow: 0 0 3px 1px #39ff14, 0 0 2px 0 #fff, 0 0 3px 0px #ce4257, 0 0 6px 1px #39ff14;
  text-shadow: none; }

.drumkit-key-pad {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 30px;
  padding: 30px; }

body {
  margin: 0;
  padding: 0; }

.app {
  background-color: #482969;
  min-height: 100vh;
  width: 100vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .app h1 {
    margin: 0;
    padding: 15px 0;
    font-family: "Londrina Outline", cursive;
    font-size: 54px;
    color: #ffffff;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    text-shadow: 0 0 3px #ce4257, 0 0 5px #ce4257, 0 0 15px #ce4257, 0 0 25px #ce4257; }
  .app .container {
    padding: 30px 7px 0 7px;
    box-shadow: 0 0 1px 1px rgba(255, 255, 255, 0.1);
    border-radius: 6px; }
  .app .audio-track {
    opacity: 0; }

